@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/Montserrat/MontserratRegular.woff2") format("woff2"), url("../fonts/Montserrat/MontserratRegular.woff") format("woff"), url("../fonts/Montserrat/MontserratRegular.ttf") format("ttf"), url("/fonts/Montserrat/MontserratRegular.eot") format("eot");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/Montserrat/MontserratMedium.woff2") format("woff2"), url("../fonts/Montserrat/MontserratMedium.woff") format("woff"), url("../fonts/Montserrat/MontserratMedium.ttf") format("ttf"), url("/fonts/Montserrat/MontserratMedium.eot") format("eot");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/Montserrat/MontserratSemiBold.woff2") format("woff2"), url("../fonts/Montserrat/MontserratSemiBold.woff") format("woff"), url("../fonts/Montserrat/MontserratSemiBold.ttf") format("ttf"), url("/fonts/Montserrat/MontserratSemiBold.eot") format("eot");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../fonts/Oswald/OswaldBold.woff2") format("woff2"), url("../fonts/Oswald/OswaldBold.woff") format("woff"), url("../fonts/Oswald/OswaldBold.ttf") format("ttf"), url("/fonts/Oswald/OswaldBold.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto/RobotoBold.woff2") format("woff2"), url("../fonts/Roboto/RobotoBold.woff") format("woff"), url("../fonts/Roboto/RobotoBold.ttf") format("ttf"), url("/fonts/Roboto/RobotoBold.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}
.sets__slider, .sets__header {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px;
}

.sets-item__content, .sets-item {
  display: flex;
  flex-direction: column;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  color: #434242;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #000;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #000;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1248px;
  padding: 0 24px;
  margin: 0 auto;
}

/* Components */
.page {
  overflow: hidden;
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 112px;
}

.logo {
  margin-right: 20px;
  flex-shrink: 0;
  width: 80px;
  height: 80px;
}

.site-nav {
  display: flex;
}
.site-nav__item {
  margin-right: 33px;
}
.site-nav__item:last-child {
  margin-right: 12px;
}
.site-nav__link {
  padding: 3px 10px;
  font-weight: 500;
  color: #fff;
  position: relative;
}
.site-nav__link:after {
  content: "";
  display: block;
  width: calc(100% - 20px);
  height: 2px;
  position: absolute;
  top: 100%;
  left: 50%;
  background-color: #961914;
  opacity: 0;
  transform: translateX(-50%);
  transition: opacity 0.2s linear;
}
.site-nav__link:hover:after {
  opacity: 1;
}

.burger {
  display: none;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 800px;
  margin-bottom: 160px;
  color: #fff;
  background: #434242 url("../images/intro-bg.jpg") center no-repeat;
  background-size: cover;
  position: relative;
}
.intro__inner {
  width: 100%;
  max-width: 460px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}
.intro__title {
  margin-bottom: 25px;
  width: 100%;
  max-width: 340px;
  font-size: 20px;
  font-weight: 400;
  order: 2;
}
.intro__subtitle {
  margin-bottom: 12px;
  font-family: "Oswald", sans-serif;
  font-size: 54px;
  font-weight: 700;
  line-height: 1.3;
  letter-spacing: 0.02em;
  order: 1;
}
.intro__text-mobile {
  display: none;
}
.intro__decorative {
  height: 98px;
  position: absolute;
  top: 63px;
  left: 156px;
  z-index: -1;
  transform: rotate(5deg);
}
.intro__btn {
  margin-right: auto;
  order: 3;
}
.intro__zooov {
  width: 168px;
  height: 548px;
  position: absolute;
  top: 160px;
  left: calc(100% - 147px);
  z-index: 2;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  padding: 13px 42px;
  font-size: inherit;
  font-weight: 600;
  line-height: 1.4;
  color: #fff;
  text-align: center;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s linear;
}
.btn--primary {
  background-color: #961914;
}
.btn--primary:hover {
  background-color: #75110D;
}
.btn--secondary {
  color: #961914;
  border: 1px solid #961914;
  background-color: transparent;
}
.btn--secondary:hover {
  border-color: #EAD1D0;
  background-color: #EAD1D0;
}

.section {
  padding-bottom: 160px;
}
.section__title {
  font-family: "Oswald", sans-serif;
  font-size: 46px;
  line-height: 1.3;
  font-weight: 700;
  letter-spacing: 0.02em;
  color: #000;
}
.section__description {
  color: #000;
}

.about-product {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: auto auto;
  column-gap: 24px;
  row-gap: 80px;
}
.about-product__content {
  grid-column: 1/span 6;
  padding-top: 56px;
}
.about-product__title {
  margin-bottom: 32px;
}
.about-product__text {
  margin-bottom: 24px;
}
.about-product__list-title {
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.3;
  color: #000;
}
.about-product__item {
  padding-left: 12.5px;
  position: relative;
}
.about-product__item:before {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #961914;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.about-product__item:not(:last-child) {
  margin-bottom: 14px;
}
.about-product__image {
  grid-column: 7/-1;
}
.about-product__slider {
  grid-column: 1/-1;
}

.info-slider {
  padding: 32px 0;
  background-color: #EAD1D0;
  background-image: url("../images/info-decorative-line.png");
  background-repeat: no-repeat;
  background-position: center;
}
.info-slider__wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1040px;
}
.info-slider__item {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 449px;
  height: 225px;
  flex-grow: 1;
}
.info-slider__item:first-of-type {
  margin-right: 40px;
}
.info-slider__title {
  margin-bottom: 18px;
  width: 100%;
  font-size: 14px;
  line-height: 2;
  font-weight: 600;
  color: #000;
  text-transform: uppercase;
}
.info-slider__list:first-of-type {
  margin-right: 72px;
}
.info-slider__list-item {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 2.5;
  font-weight: 500;
}
.info-slider__list-item span {
  margin-left: 16px;
}
.info-slider__icon {
  width: 20px;
  height: 20px;
}

.sets {
  padding-top: 83px;
  padding-bottom: 72px;
  background-color: #FFF4F3;
}
.sets__header {
  margin-bottom: 28px;
  position: relative;
  z-index: 1;
}
.sets__title {
  grid-column: 1/span 6;
  grid-row: 1/span 1;
}
.sets__decorative {
  width: 242px;
  height: 72px;
  position: absolute;
  top: 0px;
  left: -37px;
  z-index: -1;
  transform: rotate(-7deg);
}
.sets__description {
  grid-column: 7/-1;
  grid-row: 1/span 1;
  margin-top: 12px;
}
.sets__item:first-child {
  grid-column: 1/span 3;
}
.sets__item:nth-child(2) {
  grid-column: 4/span 3;
}
.sets__item:nth-child(3) {
  grid-column: 7/span 3;
}

.sets-item {
  position: relative;
}
.sets-item__chevron {
  margin: 0;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  color: #EC7973;
  text-align: center;
  background-color: #FFF4F3;
  border-radius: 8px;
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 1;
}
.sets-item__link {
  margin-bottom: 25px;
}
.sets-item__img {
  width: 100%;
  height: 335px;
  object-fit: cover;
}
.sets-item__content {
  text-align: left;
}
.sets-item__title {
  margin-bottom: 9px;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  line-height: 1.3;
  color: #000;
}
.sets-item__list {
  margin-bottom: 8px;
  font-size: 14px;
}
.sets-item__info {
  margin-bottom: 11px;
  font-size: 13px;
  line-height: 1.3;
  color: #9F9E9D;
}
.sets-item__price {
  margin: 0;
  display: flex;
  align-items: center;
  line-height: 1.3;
}
.sets-item__price s {
  display: inline-block;
  margin-right: 8px;
}
.sets-item__price span {
  font-weight: 600;
  color: #000;
}
.sets-item__btn {
  margin-top: 17px;
  margin-right: auto;
}

.calculator {
  padding: 35px 25px 25px;
  grid-column: 10/-1;
  background-color: #EAD1D0;
  background-image: url("../images/calculator-decorative-element.png");
  background-repeat: no-repeat;
  background-position: center;
}
.calculator__description {
  margin-bottom: 42px;
  font-size: 23px;
  font-weight: 500;
  letter-spacing: 0.02em;
  color: #000;
}
.calculator--mobile {
  display: none;
  grid-column: 1/-1;
  background-color: #EAD1D0;
}
.calculator--mobile__wrapper {
  max-width: 282px;
  margin: 0 auto;
  padding: 32px 0;
  text-align: center;
}
.calculator--mobile__description {
  margin-bottom: 24px;
  font-size: 20px;
}